import React from "react";

import "./CitiesSectionFive.css";

import pic1 from "./pic3.png";

export default function CitiesSectionFive(prop) {
  return (
    <div className="cities-section-five-main-container" id="social-media">

      <div className="cities-section-five-main-container-big-circle"></div>

      <div className="cities-section-five-main-container-sub-container-one">

        <div className="cities-section-five-main-container-sub-container-one-triangle"></div>

        <div className="cities-section-five-main-container-sub-container-one-circle"></div>

        <img src={pic1} />

      </div>

      <div className="cities-section-five-main-container-sub-container-two">

        <div className="cities-section-five-main-container-sub-container-two-circle"></div>

        <div className="cities-section-five-main-container-sub-container-two-triangle"></div>

        <div className="cities-section-five-main-container-sub-container-two-heading">
          <h2>
            Social Media <br />
            Marketing
          </h2>
        </div>

        <div className="cities-section-five-main-container-sub-container-two-paragraph">
          <p>
            {prop.paragraph}
          </p>
        </div>

        <div className="cities-section-five-main-container-sub-container-two-button">
          <button>Learn More</button>
        </div>

      </div>
    </div>
  );
}
